.settings {
   display: flex;
}

.settingsWrapper {
   flex: 9;
   padding: 20px;
}

.settingsTitle {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.settingUpdateTitle {
   font-size: 30px;
   margin-bottom: 20px;
   color: lightcoral;
}

.settingDeleteTitle {
   color: red;
   font-size: 12px;
   cursor: pointer;
}

.settingForm {
   display: flex;
   flex-direction: column;
   margin-left: 10px;
}
.settingsPP {
   display: flex;
   align-items: center;
   margin: 10px 0px;
}

.settingsPP > img {
   width: 90px;
   height: 90px;
   border-radius: 50%;
   object-fit: cover;
}

.settingsPPIcon {
   display: flex;
   width: 25px;
   height: 25px;
   border-radius: 50%;
   background-color: lightcoral;
   color: #fff;
   align-items: center;
   justify-content: center;
   margin-left: 10px;
   cursor: pointer;
}

.settingForm > label {
   font-size: 20px;
   margin-top: 20px;
}

.settingForm > input {
   color: gray;
   margin: 10px 0px;
   height: 30px;
   border: none;
   border-bottom: 1px solid lightgray;
   outline: none;
}

.settingSubmit {
   width: 150px;
   align-self: center;
   border: none;
   border-radius: 10px;
   color: #fff;
   background-color: teal;
   padding: 10px;
   margin-top: 20px;
   cursor: pointer;
}
